import MasterPage from '../componentes/MasterPage'
import Noticias from '../componentes/Noticias'
import fetch from 'isomorphic-unfetch'
import Precio from '../componentes/Precio'
const Index = (props) => (
        <MasterPage>
            <div className='row'>
                <div className='col-12'>
                    <Precio
                        precioActual = {props.precioActual}
                        precio1Semana = {props.precio1Semana}
                        precio1Hora = {props.precio1Hora}
                        precio1Dia = {props.precio1Dia}
                    />
                </div>
                <div className='col-md-8'>
                    <Noticias
                        noticias = {props.noticias}
                    />
                    {console.log(props.eventos)}
                </div>
                <div className='col-md-4'>
                    <h2>Precio del Bitcoin</h2>
                </div>
            </div>
        </MasterPage>
    )

    Index.getInitialProps= async ()=> {
        const consultaActual = await fetch('https://api.binance.com/api/v3/avgPrice?symbol=BTCUSDT')
        const consulta1Semana = await fetch('https://api.binance.com/api/v3/klines?symbol=BTCUSDT&interval=1w&limit=2')
        const consulta1Hora = await fetch('https://api.binance.com/api/v3/klines?symbol=BTCUSDT&interval=1h&limit=2')
        const consulta1Dia = await fetch('https://api.binance.com/api/v3/klines?symbol=BTCUSDT&interval=1d&limit=2')

        const token = 'GSrBp6V778cPgSpv'
        const urlEvento=`http://api.eventful.com/json/events/search?app_key=${token}&keyword=bitcoin&location=peru`
        const resultadoEvento = await fetch (urlEvento)
        
        const apiKey = '2d9648fe03874594aaf92bc2824ef0ee'
        const url = `http://newsapi.org/v2/everything?q=bitcoin&sortBy=publishedAt&apiKey=${apiKey}&language=es`
        const resultadoNoticias = await fetch(url)
       
        const resultadoActual =await consultaActual.json()
        const resultado1Semana =await consulta1Semana.json()
        const resultado1Hora =await consulta1Hora.json()
        const resultado1Dia =await consulta1Dia.json()
        const noticias = await resultadoNoticias.json()
        const eventos = await resultadoEvento.json()
        return {
            precioActual : resultadoActual.price,
            precio1Semana : resultado1Semana[0][4],
            precio1Hora : resultado1Hora[0][4],
            precio1Dia : resultado1Dia[0][4],
            noticias : noticias.articles,
            eventos : eventos
        }
    }

export default Index;